import { Fragment, useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useTranslation } from "react-i18next"
import Table from "./Table"
import DropDown from "./DropDown"
import { Row, Col } from "reactstrap"
import StatsHorizontal from "@components/widgets/stats/StatsHorizontal"
import { User, UserPlus, UserCheck, UserX } from "react-feather"
import Icon from "@mui/material/Icon"
import AddCardOutlinedIcon from "@mui/icons-material/AddCardOutlined"
import "./style.scss"
import Breadcrumbs from "@components/breadcrumbs"

import {
  storeName,
  getAlwaysGrid,
  getApiSummaryData,
  getSummaryData,
  pageTitle,
  isTreeLayout
} from "./store"
import { Skeleton } from "@mui/material"

const List = ({
  filtersCustomAdd = {},
  lookupMode = false,
  lookupMode_fieldName = "",
  lookupMode_dropdown = false,
  lookupMode_isMulti = false,
  lookupMode_onChange,
  lookupMode_defaultValue,
  lookupMode_isM2MDefValFromGrid,
  lookupMode_defaultFilter
}) => {
  const { t } = useTranslation()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])
  const [searchTerm, setSearchTerm] = useState("")

  const handleFilter = (event) => {
    // console.log("handleFilter")
    setSearchTerm(event)
    // debounceFn(event)
  }

  useEffect(() => {
    if (
      getApiSummaryData &&
      (store.dataSummary?.length === 0 ||
        (!store.isLoadingSummary && getAlwaysGrid))
    ) {
      dispatch(getSummaryData({}))
    }
  }, [dispatch, store.dataSummary?.length])

  const colors = [
    "primary",
    "success",
    "warning",
    "danger",
    "info",
    "secondary"
  ]

  return !lookupMode_dropdown ? (
    <Fragment>
      <div className="content-header row">
        {!lookupMode && (
          <Breadcrumbs
            title={t(pageTitle)}
            data={[{ title: t("App Setting") }, { title: t(pageTitle) }]}
          />
        )}
        {/* <div className="content-header-right text-md-end col-md col-12 mb-2 ">
          <div className="d-flex align-items-center mb-sm-0 mb-1 me-1">
            <label className="mb-0" htmlFor="search-invoice">
              {t("Search")}:
            </label>
            <Input
              id="search-invoice"
              className="ms-50 w-100"
              type="text"
              value={searchTerm}
              onChange={(e) => handleFilter(e.target.value)}
            />
          </div>
        </div> */}
      </div>
      <div className="app-user-list">
        {getApiSummaryData && !lookupMode && (
          <Row>
            {store.dataSummary?.map((item, index) => {
              return (
                <Col>
                  <StatsHorizontal
                    color={colors[index]}
                    statTitle={`${t(item.title)}`}
                    icon={
                      item.icon ? (
                        <Icon
                          baseClassName="material-icons-outlined"
                          fontSize="20"
                        >
                          {item.icon}
                        </Icon>
                      ) : (
                        <Icon
                          baseClassName="material-icons-outlined"
                          fontSize="20"
                        >
                          lightbulb
                        </Icon>
                      )
                    }
                    renderStats={
                      store.isLoadingSummary ? (
                        <Skeleton
                          sx={{ bgcolor: "#eef2f8" }}
                          animation="wave"
                          height={25}
                          width={40}
                          className="fw-bolder mb-75"
                        />
                      ) : (
                        <h3 className="fw-bolder mb-75">
                          {item.count_format ?? item.count}
                        </h3>
                      )
                    }
                  />
                </Col>
              )
            })}
          </Row>
        )}
        <Table
          filtersCustomAdd={filtersCustomAdd}
          lookupMode={lookupMode}
          lookupMode_fieldName={lookupMode_fieldName}
          lookupMode_defaultValue={lookupMode_defaultValue}
          lookupMode_onChange={lookupMode_onChange}
          lookupMode_isMulti={lookupMode_isMulti}
          lookupMode_isM2MDefValFromGrid={lookupMode_isM2MDefValFromGrid}
          lookupMode_defaultFilter={lookupMode_defaultFilter}
        />
      </div>
    </Fragment>
  ) : (
    <DropDown
      lookupMode_fieldName={lookupMode_fieldName}
      lookupMode_onChange={lookupMode_onChange}
      lookupMode_defaultValue={lookupMode_defaultValue}
      lookupMode_isMulti={lookupMode_isMulti}
      lookupMode_isM2MDefValFromGrid={lookupMode_isM2MDefValFromGrid}
    />
  )
}

export default List
